@import "../../_colors.scss";
.sort-list-container{
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: -2em auto 2.4em;
  padding: 1em 2em;
  width: 80%;
  background: #fff;
  color: $des-dark-cyan;
  box-shadow: 0 1em 1em -.9em $des-dark-cyan;
  ul{
    flex-wrap: wrap;
  }
  .sort-list__item{
    display: flex;
    align-items: center;
    margin-right: 1em;
    border-radius: .2em;
    background-color: $light-grayish-cyan-filter;
    .sort-list__item-name{
      padding: 0em .5em;
    }
    .sort-list__item-del{
      background-color: $very-dark-grayish-cyan;
      padding: .5em;
      border-radius: 0em .2em .2em 0;
      cursor: pointer;
      img{
        display: block;
      }
      &:hover{
        background-color: $dark-grayish-cyan;
      }
    }
  }
  .clear-btn{
    font-size: 1em;
    text-decoration: underline;
    cursor: pointer;
  }
}
