@import "../_colors.scss";
$info-margin-right: .8em;
.company-listing{
  display: flex;
  align-items: center;
  margin: 2em auto;
  padding: 2em;
  width: 80%;
  border-radius: 1em;
  box-shadow: 0 .5em 1em -.2em $des-dark-cyan;
  .company-info{
    padding-left: 1em;
    .company-info__position{
      cursor: pointer;
      transition: color .1s ease;
      font-size: 1.7em;
      margin: .2em 0;
      &:hover{
        color: $des-dark-cyan;
      }
    }
    .sub-info .sub-info__list li{
      color: $des-dark-cyan;
      margin-right: $info-margin-right;
    }
    .pro-info{
      ul{
        align-items: center;
      }
      li{
        margin-right: $info-margin-right;
      }
      .pro-info__new, .pro-info__featured{
        padding: .3em .5em .1em;
        color: $light-grayish-cyan-bg;
        border-radius: 1em;
      }
      .pro-info__new{
        background-color: $des-dark-cyan;
      }
      .pro-info__featured{
        background-color: $dark-grayish-cyan;
      }
    }
  }
  .requirements{
    margin-left: auto;
    flex-wrap: wrap;
    .requirements__item{
      margin-right: 1em;
      padding: .4em;
      border-radius: .3em;
      color: $des-dark-cyan;
      transition: background-color .2s ease, color .2s ease;
      cursor: pointer;
      line-height: 100%;
      &:hover{
        background-color: $des-dark-cyan;
        color: $light-grayish-cyan-bg;
      }
    }
  }
}
.is-featured{
    border-left: 5px solid $des-dark-cyan;
}
@media only screen and (max-width: 768px){
  .company-listing{
    flex-direction: column;
    align-items: start;
    padding: 1em 1em;
    margin: 4em auto;
    .company-info{
      padding-left: 0;
      padding-bottom: 1.2em;
      .company-info__position{
        font-size: 1.2em;
        margin: .7em 0;
      }
    }
    .company-img-container{
      margin-top: -3em;
      width: 50px;
      img{
        width: 100%;
      }
    }
    .requirements{
      margin-left: 0;
      padding-top: 1.2em;
      border-top: 1px solid $des-dark-cyan;
    }
  }
}
